//Modules
@use "sass:map";
@use "sass:string";
@use "sprucecss/scss/spruce" as *;

$images-br: (
  'sm': spacer(xxs),
  'md': spacer(xs),
  'lg': spacer(s),
  'xl': spacer(m),
  'full': 9999px,
);

@each $key, $value in $images-br{
  .img-rounded-#{$key}{
    border-radius: $value;
  }
}